import {HttpService} from './http.service';
import {Injectable} from '@angular/core';
import {Store, select} from '@ngrx/store';
import {selectToken, userLoggedInSuccess, userLoggedOutSuccess} from '../stores/user.store';
import {SERVER} from '../config';

@Injectable()
export class UserService {
  constructor(private http: HttpService,
              private store: Store<any>) {
  }

  /**
   *
   *@param  useraccount		string	
    @param  password		string	

   id	string	ID
   loginAcct	string	
   phoneNum	string	
   accountStatus	int	 1 2
   salesmanToken	string	token. 
   *
   * */
  public login({useraccount, password}) {
    let seq = this.http.post('/users/login', {
      useraccount,
      password
    }).share();
    seq.subscribe((user) => {
      this.store.dispatch(userLoggedInSuccess(user));
    }, () => {

    });
    return seq;
  }

  public logout(useraccount) {
    let seq = this.http.post('/users/logout', {useraccount:useraccount}).share();
    seq.subscribe((user) => {
      this.store.dispatch(userLoggedOutSuccess());
    }, () => {

    });
    return seq;

  }


  /** 
   * 
   * @param userAccount		string	
   * @return isExist	int	0  1 
   * */
  inspectUserAccount(userAccount){
    return this.http.get(`/users/exists/${userAccount}`);
  }

  /** 
   *  
   * @param phoneNum		string	
   * @param userAccount		string	
    @param opType		string	  RESET_PSD
   * @return isExist	int	0  1 
   * */
  sendAuthMessage({userAccount,phoneNum,opType='RESET_PSD'}){
    return this.http.get(`/users/${userAccount}/phone/verifyCode`,{phoneNum,opType});
  }


  /**
   *():
   * @param userAccount		string	
   @param phoneNum		string	
   @param code		string	
   @param opType		string	RESET_PSD

   @return session	string	session  
   * */
  verifyCode({userAccount,phoneNum,code,opType='RESET_PSD'}){
    return this.http.post(`/users/${userAccount}/phone/verifyCode`,{phoneNum,code,opType});
  }
  /** 
   * 
   * @param userAccount		string	
   @param  phoneNum		string	
   @param  password		string	
   @param  session		string	ID

   @return groupid	int	id12
   * */
  resetPwd({userAccount,phoneNum,password,session}){
    return this.http.post(`/users/${userAccount}`,{phoneNum,password,session});
  }

  /** 
   * 
   * @param  userAccount		string	
   @param  phoneNum		string	
   @param  code		string	
   @param  session		string	session
   @param  newPhoneNum		string	

   * */
  getNewAuthMessage({userAccount,phoneNum,code,session,newPhoneNum}){
    return this.http.get(`/users/${userAccount}/phone/new/verifyCode`,{phoneNum,code,session,newPhoneNum});
  }
  /** 
   *   
   *  @param  userAccount		string	
   @param  phoneNum		string	
   @param  newPhoneNum		string	
   @param  newVerifyCode		string	
   return session,  
   * */
  verifyNewAuthMessage({userAccount,phoneNum,newVerifyCode,newPhoneNum}){
    return this.http.get(`/users/${userAccount}/phone/new/verifyCode`,{phoneNum,newVerifyCode,newPhoneNum});
  }





}



