import {HttpService} from './http.service';
import {Injectable} from '@angular/core';


@Injectable()
export class ApplicationRecordService {
  constructor(private http: HttpService,) {
  }

  /**
   *
   * loans
   * loanId  String  ID
   applyTime  String  
   applyStatus  int   
   customerName  String  
   customerId  String  ID
   repaymentTimes  int  
   *
   * */
  public fetchLoansList({pageNo, pageSize} = { pageNo: 0, pageSize: 0 }) {
    return this.http.get('/loans',{pageNo,pageSize}).share();
  }

}



