import {HttpService} from './http.service';
import {Injectable} from '@angular/core';


@Injectable()
export class MyService {
  constructor(private http: HttpService,) {
  }


  /** 
   * @param userAccount    string  
   @param idCard  string  
   @param pageNo  string  
   @param pageSize  string 

   loanId  String  ID
   applyTime  String  
   applyStatus  String   
   * */

  fetchLoansList(userAccount: string, idCard: string, pageNo: number = 0, pageSize = 20) {
    const url = `/users/${userAccount}/customers/loans`;
    return this.http.get(url, {idCard, pageNo, pageSize})
  }


  /** 
   * @param userAccount    string  
   @param pageNo  string  
   @param pageSize  string 

   loanId  String  ID
   applyTime  String  
   applyStatus  String   
   * */

  fetchCustomersList(userAccount: string, pageNo: number = 0, pageSize = 20) {
    const url = `/users/${userAccount}/customers`;
    return this.http.get(url, {pageNo, pageSize})
  }

  /** 
   * @param userAccount    string  
   * @param queryParam    string  
   @param pageNo  string  
   @param pageSize  string 

   name	String	
   time	String	
   address	String	
   phoneNum	String	
   * */

  fetchReservationsList(userAccount: string, queryParam?: string, pageNo: number = 0, pageSize = 20) {
    const url = `/users/${userAccount}/reservations`;
    return this.http.get(url, {pageNo, pageSize,queryParam})
  }

  /** 
   * @param userAccount    string  
   @param pageNo  string  
   @param pageSize  string 

   msgId	String	ID
   name	String	
   time	String	
   address	String	
   phoneNum	String	
   * */

  fetchMsgsList(userAccount: string,  pageNo: number = 0, pageSize = 20) {
    const url = `/users/${userAccount}/msgs`;
    return this.http.get(url, {pageNo, pageSize})
  }

  /** 
   * @param userAccount    string  
   * @param msgId    string  ID
   title	string	
   notifyTime	string	
   other	string	
   * */

  fetchMsgsDetail(userAccount: string,msgId:string) {
    const url = `/users/${userAccount}/msgs/${msgId}`;
    return this.http.get(url, )
  }


}



