import {Component} from '@angular/core';
import {AlertController, NavController, NavParams} from 'ionic-angular';
import {IdentityInfoProvider} from "./identity-info.service";

/**
 * Generated class for the IdentityInfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage({
//   name: 'identity-info'
// })
@Component({
  selector: 'page-identity-info',
  templateUrl: 'identity-info.html',
})
export class IdentityInfoPage {
  itemsFirst = [
    {title: '', key: 'status', value: ''},
    {title: '', key: 'education', value: ''},
    {title: '', key: 'marriage', value: ''},

  ];
  itemsSecond = [
    // {title:'' , key:'residenceDate',value:''},
    {title: '', key: 'boarding', value: ''},
  ];

  testRadioOpen: boolean;
  testRadioResult;

  // 
  educationList = [
    {type: 'radio', label: '', value: 'boshi', checked: false},
    {type: 'radio', label: '', value: 'suoshi', checked: false},
    {type: 'radio', label: '', value: 'benke', checked: false},
    {type: 'radio', label: '', value: 'dazhuan', checked: false},
    {type: 'radio', label: '', value: 'gaozhong', checked: false},
    {type: 'radio', label: '', value: 'chuzhong', checked: false},
  ];

  // 
  marriageList = [
    {type: 'radio', label: '', value: 'weihun', checked: false},
    {type: 'radio', label: '', value: 'yihun', checked: false},
    {type: 'radio', label: '/', value: 'fenju', checked: false},
    {type: 'radio', label: '', value: 'qita', checked: false}
  ];

  // 
  boardingList = [
    {type: 'radio', label: '', value: 'ziyou', checked: false},
    {type: 'radio', label: '', value: 'zupin', checked: false},
    {type: 'radio', label: '', value: 'jiaren', checked: false},
    {type: 'radio', label: '', value: 'gongsi', checked: false},
    {type: 'radio', label: '', value: 'diya', checked: false}
  ];

  // 
  residenceDate = '';

  residenceColumns: any = [
    {
      options:
        [
          {text: '0', value: '0'},
          {text: '1', value: '1'},
          {text: '2', value: '2'},
          {text: '3', value: '3'},
          {text: '4', value: '4'},
          {text: '5', value: '5'},
          {text: '6', value: '6'},
          {text: '7', value: '7'},
          {text: '8', value: '8'},
          {text: '9', value: '9'},
          {text: '10', value: '10'},
          {text: '10', value: '11'},
        ]
    },
    {
      options: [
        {text: '0', value: '0'},
        {text: '1', value: '1'},
        {text: '2', value: '2'},
        {text: '3', value: '3'},
        {text: '4', value: '4'},
        {text: '5', value: '5'},
        {text: '6', value: '6'},
        {text: '7', value: '7'},
        {text: '8', value: '8'},
        {text: '9', value: '9'},
        {text: '10', value: '10'},
        {text: '11', value: '11'},
        {text: '12', value: '12'}
        ]
    },
  ];

  // 
  city = '';
  AddressColumns: any = [];

  // 
  addressDetail:string = '';
  constructor(public navCtrl: NavController,
              public alertCtrl: AlertController,
              public identityInfoProvider: IdentityInfoProvider,
              public navParams: NavParams) {

    this.identityInfoProvider.getCitys().subscribe(res => {
      console.log('res', res);
      this.AddressColumns = res;
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad IdentityInfoPage');
  }

  choice(key) {

    console.log('key', key);
    switch (key) {
      // case 'education':this.choiceEducation();break;
      case 'education':
        this.renderRadio('', this.educationList, 'education');
        break;
      case 'marriage':
        this.renderRadio('', this.marriageList, 'marriage');
        break;
      case 'boarding':
        this.renderRadio('', this.boardingList, 'boarding');
        break;
    }
  }


  renderRadio(title, list, key) {
    let alert = this.alertCtrl.create();
    alert.setTitle(title);
    list.forEach((val) => {
      alert.addInput(val);
    });

    alert.addButton('');
    alert.addButton({
      text: '',
      handler: data => {
        console.log('Radio data:', data);
        this.checkedData(list, data, key);

        this.testRadioOpen = false;
        this.testRadioResult = data;
      }
    });
    alert.present().then(() => {
      this.testRadioOpen = true;
    });
  }

  checkedData(list, value, key) {
    list.forEach(val => {
      val.checked = false;
      if (val.value === value) {
        val.checked = true;
        console.log(val);
        this.renderListValue(this.itemsFirst, key, val.label);
        this.renderListValue(this.itemsSecond, key, val.label);
      }
    });
  }

  renderListValue(list, key, value) {
    list.forEach(val => {
      if (val.key === key) {
        val.value = value;
      }
    });
  }

  // 
  save(){
    //city 
    console.log('city ',this.city);
    // 
    console.log('addressDetail ',this.addressDetail);
    // 
    console.log('residenceDate ',this.residenceDate);
    // 
    console.log('educationList ',this.educationList);
    // 
    console.log('marriageList ',this.marriageList);
    // 
    console.log('boardingList ',this.boardingList);
    console.log('educationList ',this.identityInfoProvider.getListCheckedValue(this.educationList));
    console.log('educationList ',this.identityInfoProvider.getListCheckedValue(this.marriageList));
    console.log('educationList ',this.identityInfoProvider.getListCheckedValue(this.boardingList));

  }



}



